.wp-site-blocks .site-header {

	// Needed until Gutenberg offers responsive design options
	// TODO: Ref to responsive design options issue
	@include break-small-only(){
		.site-brand {
			display: contents;
		}
		.wp-block-site-logo {
			flex-basis: 100%;
			margin-bottom: calc( 2 * var(--wp--custom--gap--vertical));
			margin-top: var(--wp--custom--gap--vertical);
			text-align: center;
		}
	}

}

.wp-block-site-logo {
	img {
		max-height: 200px;
		width: auto;
		transition: all 0.3s;
	}
}

.wp-site-blocks .header-standard {
	margin-top: 0 !important;
}

.wp-site-blocks .wp-block-group.header-sticky {
	margin-top: 0 !important;
	overflow: visible;
	padding-top: 0;
	padding-bottom: 0;
	position:fixed; // for header scroll
 	width: 100%;
 	z-index:999;

	&.block-editor-block-list__block {
		position: relative;
	}

	.site-header {
		transition: all 0.3s;
	}

	&.header-sticky-small {
		.site-header {
			padding-top: 5px !important;
			padding-bottom: 5px !important;
		}

		.wp-block-site-logo {
			img {
				max-height: 60px;
				width: auto;
			}
		}

		@include break-small-only(){

		}
	}
}
