img {
    border: 0;
    height: auto;
    -ms-interpolation-mode: bicubic;
    max-width: 100%;
    vertical-align: middle;
}

figure {
    margin: 0 0 var(--wp--custom--gap--vertical) 0;
}

figcaption {
    display: block !important; // overrides gutenberg issue
    font-size: var(--wp--preset--font-size--small);
    font-style: italic;
    font-weight: normal;
    margin: .5em 0 0 0;
    padding: 0;
}
